/**
 *     API
 *
 * @param {Object} params 
 * @param {String} params.nameMethod    API
 * @param {String} params.method  
 * @param {Object} params.body  
 * @param {String} params.id Id  
 * @param {Object} settings 
 * @return {Promise}  
 */
function buildPromise(params, settings) {
  let url = `${settings.apiURL}${settings.apiIdGroup}/${params.nameMethod}`;

  const options = {
      method: params.method,
      headers: {
        authorization: settings.apiToken,
      },
    };

  if (params.id !== null) {
    url = url + `/${params.id}`;
  }
  if (params.body !== null) {
    options.headers['Content-Type'] = 'application/json';
    options.body = JSON.stringify(params.body);
  }

  return fetch(url, options)
}

/**
 *   API   
 *
 * @param {Object} settings 
 * @return {Promise}
 */
export function getProfileAndCard(settings) {
  return Promise.all(
  [buildPromise({
             nameMethod: settings.methodProfile,
             method: 'GET',
             body: null,
             id: null
           },
           settings),
         buildPromise({
             nameMethod: settings.methodCard,
             method: 'GET',
             body: null,
             id: null
           },
           settings),
        ]);
}

/**
 *   API 
 *
 * @param {Object} data  
 * @param {String} data.name  
 * @param {String} data.about  
 * @param {Object} settings 
 * @return {Promise}
 */
export function setProfile(data, settings) {
  return buildPromise({
      nameMethod: settings.methodProfile,
      method: 'PATCH',
      body: {name: data.name, about: data.about},
      id: null
    },
    settings);
}

/**
 *   API  
 *
 * @param {Object} data  
 * @param {String} data.name  
 * @param {String} data.link URL 
 * @param {Object} settings 
 * @return {Promise}
 */
export function setCard(data, settings) {
  return buildPromise({
      nameMethod: settings.methodCard,
      method: 'POST',
      body: {name: data.name, link: data.link},
      id: null
    },
    settings);
}

/**
 *   API 
 *
 * @param {Object} data  
 * @param {String} data.id Id 
 * @param {HTMLElement} data.element   
 * @param {Object} settings 
 * @return {Promise}
 */
export function deleteCard(data, settings) {
  return  buildPromise({
    nameMethod: settings.methodCard,
    method: 'DELETE',
    body: null,
    id: data.id
  },
  settings);
}

